.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* App.css */
.features-container {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: #fff; /* Change background color as needed */
}

.feature-box {
  flex: 1;
  margin: 0 10px;
  padding: 20px;
  background-color: #f4f4f4;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: background-color 0.3s; /* Smooth transition for background color */
  position: relative; /* Ensures the hover effect is contained within the box */
  overflow: hidden; /* Prevents content overflow */
}

.feature-box h3 {
  color: #333; /* Dark grey color for titles */
  margin-bottom: 10px;
}

.feature-box p {
  color: #666; /* Lighter text color for descriptions */
}

.feature-box:hover p {
  color: #0b0808; /* Lighter text color for descriptions */
}

.feature-box:hover {
  background-color: #fc2f70; /* Change background to red on hover */
  color: #1a1414;
  /* Optional: Change text color on hover */
}

.feature-box img {
  width: 100%;
  height: auto;
  display: block; /* Ensures the image fits the container */
  transition: transform 0.3s; /* Smooth transition for transformations */
  transform: scale(1); /* Normal scale */
}

.feature-box:hover img {
  transform: scale(1.05); /* Slightly increase the scale of the image on hover */
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #1c1c1e;
  color: #fff;
}

.header {
  background-color: #1c1c1e;
  padding: 20px 40px;
  text-align: center;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.feature div {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 50px,
}
.header nav a {
  color: #fff;
  text-decoration: none;
  margin-right: 20px;
  font-weight: bold;
}

.main-image {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

.main-section {
  text-align: center;
  padding: 50px 20px;
}

.main-section h1 {
  font-size: 3rem;
  color: #fc2f70;
}

.main-section p {
  font-size: 1.2rem;
  color: #ccc;
}

.feature,
.about {
  text-align: center;
  margin: 40px 20px;
}

.feature img,
.about img {
  width: auto;
  height: auto;
}

button {
  padding: 10px 20px;
  background-color: #fc2f70;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}
.feature p,
.about p {
  margin-bottom: 20px;
}

.link {
  color: #fc2f70;
  text-decoration: none;
}